import { create } from "zustand";

export const useStore = create((set) => ({
  user: JSON.parse(sessionStorage.getItem("users")) || null,

  // 
  setUser: (userData) => {
    sessionStorage.setItem("users", JSON.stringify(userData)); //  
    set({ user: userData });
  },
  // 
  clearUser: () => {
    sessionStorage.removeItem("users");
    set({ user: null });
  },
}));
