// join  - join.jsx
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import smallLogo from "../assets/logo/small-logo.png";

import "./css/join.scss";

function Join(props) {
  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [passwordCheck, setPasswordCheck] = useState("");
  const [isPasswordMatch, setIsPasswordMatch] = useState(false);
  const [isValid, setIsValid] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // ,   
    if (
      emailRegex.test(username) &&
      passwordRegex.test(password) &&
      password === passwordCheck
    ) {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  }, [username, password, passwordCheck]);

  useEffect(() => {
    if (password === passwordCheck && password.length > 0) {
      setIsPasswordMatch(true);
    } else {
      setIsPasswordMatch(false);
    }
  }, [password, passwordCheck]);

  const handleJoin = (e) => {
    e.preventDefault();
    if (isValid) {
      // user 
      const storedUsers = JSON.parse(localStorage.getItem("users")) || [];

      //  user  
      const newUser = {
        id: `user${String(storedUsers.length + 1).padStart(3, "0")}`, // user001 
        username: name, //    
        email: username, //  
        password: password, //  
        trips: [""], //  
      };

      //   
      const updatedUsers = [...storedUsers, newUser];

      // localStorage 
      localStorage.setItem("users", JSON.stringify(updatedUsers));

      alert(`    :)`);
      navigate("/Login");
    } else {
      alert("    .");
    }
  };

  //  - @,  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  //  - ++ 2 + 8 
  const passwordRegex =
    /^(?=.*[A-Za-z])(?=.*\d)(?=(?:.*[!@#$%^&*()\-_=+[\]{};:'",.<>/?\\|`~]){2,}).{8,}$/;

  return (
    <div className="join-container">
      <div className="join-box">
        <h2 className="join-title">
          <span className="logo-box">
            <img src={smallLogo} alt="logo" />
          </span>
        </h2>
        <form onSubmit={handleJoin}>
          <div className="inputText"></div>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="join-input"
            required
          />
          <div className="inputText"></div>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="join-input"
            required
          />
          <div className="passwordBox">
            <div className="inputText"></div>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="join-input"
              required
            />
          </div>
          <div className="passwordBox">
            <div className="inputText"></div>
            <input
              type="password"
              value={passwordCheck}
              onChange={(e) => setPasswordCheck(e.target.value)}
              className="join-input"
              required
            />
            {passwordCheck && !isPasswordMatch && (
              <div className="error">  .</div>
            )}
          </div>
          <button
            type="submit"
            className={`join-button ${isValid ? "active" : "inactive"}`}
          >
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default Join;
