// Login  - Login.jsx
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useStore } from "../stores/store.API";

//  import
import smallLogo from "../assets/logo/small-logo.png";
import google from "../assets/logo/google-logo.png";
import kakao from "../assets/logo/kakao-logo.png";
import naver from "../assets/logo/naver-logo.ico";

// scss
import "./css/login.scss";

function Login(props) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isValid, setIsValid] = useState(false);
  const navigate = useNavigate();
  const setUser = useStore((state) => state.setUser); // Zustand    

  useEffect(() => {
    const storedUsers = JSON.parse(localStorage.getItem("users")) || [];

    const matchedUser = storedUsers.find(
      (user) => user.email === username && user.password === password
    );

    if (matchedUser) {
      setIsValid(true); //   
    } else {
      setIsValid(false); //   
    }
  }, [username, password]);

  const handleLogin = (e) => {
    e.preventDefault();

    const storedUsers = JSON.parse(localStorage.getItem("users")) || [];

    const matchedUser = storedUsers.find(
      (user) => user.email === username && user.password === password
    );

    if (matchedUser) {
      sessionStorage.setItem("users", JSON.stringify(matchedUser)); //   
      setUser(matchedUser); // Zustand   
      alert(`  !`);
      navigate("/");
    } else {
      alert("    .");
    }
  };

  //  - @,  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  //  - ++ 2 + 8 
  const passwordRegex =
    /^(?=.*[A-Za-z])(?=.*\d)(?=(?:.*[!@#$%^&*()\-_=+[\]{};:'",.<>/?\\|`~]){2,}).{8,}$/;

  return (
    <div className="login-container">
      <div className="login-box">
        <h2 className="login-title">
          <span className="logo-box">
            <img src={smallLogo} alt="logo" />
          </span>
        </h2>
        <form onSubmit={handleLogin}>
          <div className="inputText"></div>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="login-input"
            required
          />
          <div className="passwordBox">
            <div className="inputText"></div>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="login-input"
              required
            />
            <div className="forgetPassword"> ?</div>
          </div>
          <button
            type="submit"
            className={`login-button ${isValid ? "active" : "inactive"}`}
          >
            
          </button>
          <div className="joinBox">
            <div>  ?</div>
            <a
              href="#"
              className="registerButton"
              onClick={(e) => {
                e.preventDefault();
                navigate("/Join");
              }}
            >
               
            </a>
          </div>
          <div className="divider">
            <span>or</span>
          </div>
          <div className="iconLoginBox">
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                alert("    .");
              }}
            >
              <img className="iconLogo" src={google} alt="google logo" />
            </a>
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                alert("    .");
              }}
            >
              <img className="iconLogo" src={kakao} alt="kakao logo" />
            </a>
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                alert("    .");
              }}
            >
              <img className="iconLogo" src={naver} alt="naver logo" />
            </a>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Login;
